@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300&display=swap');
.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: rgb(0, 0, 0);
}

.background {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: rgb(0, 0, 0);
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

#home-body,
#menu-body,
#cart-body {
    background-color: #F7F3E3;
}

#banner-div {
    position: relative;
    text-align: center;
}

#banner-img {
    width: 100%;
    height: 75vh;
}

#banner-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -45%);
    padding: 10px;
    background-color: #3f0400;
    color: bisque;
    font-size: 1.5rem;
}

#banner-h {
    position: absolute;
    top: 8%;
    left: 50%;
    transform: translate(-50%, 0%);
    padding: 10px;
    font-size: 5rem;
    text-shadow: 8px 6px 5px black;
    display: inline;
    width: fit-content;
    color: rgb(148, 0, 0)
}

#pr-text {
    font-size: 1.5rem;
    margin: 35px 25% 35px 25%;
    text-align: center;
}

#loc-div {
    display: inline-block;
    margin-left: 35%;
    margin-right: 35%;
    margin-top: 2%;
    margin-bottom: 2%;
}

#loc-img {
    width: 100%;
    max-height: 10%;
}

#loc-img-td {
    width: 45%;
}

#menu-cart-h {
    display: inline-block;
    margin-top: 10px;
    margin-bottom: 0px;
    padding: 10px;
    margin-left: 85%;
    padding-bottom: 0px;
}

#dish-card {
    display: inline-block;
    margin-left: 5%;
    margin-right: auto;
    margin-bottom: 10px;
    padding: 10px;
    width: 40%;
    box-shadow: 0px 5px 4px 0px grey;
    background-color: #E9E0D8;
}

#dish-p {
    display: inline;
    margin-right: 25px;
}

#dish-add-btn {
    display: inline;
    margin-left: 25px;
}

#dish-card-img-td {
    width: 45%;
}

#dish-card-img {
    width: 80%;
    border-radius: 20px;
    max-height: 150px;
}

.dish-type {
    font-family: 'Space Grotesk', sans-serif;
    font-size: 2rem;
    font-weight: lighter;
    margin-left: 45px;
    margin-top: 0px;
    padding-top: 20px;
}

#cart-h {
    font-family: 'Space Grotesk', sans-serif;
    margin-top: 0px;
    padding: 10px;
}

.cart-h4 {
    margin: 40px;
}

#cart-list {
    margin: 40px;
    padding-bottom: 50px;
}

#item-name {
    display: inline-block;
}

#item-price {
    display: inline-block;
    margin-left: 70%;
}

#item-remove {
    margin: 10px;
    color: bisque;
    background-color: #3f0400;
}

#cart-total-h {
    display: inline-block;
}

#cart-total {
    display: inline-block;
    margin-left: 68%;
}